import React from "react";
import ReactDOM from "react-dom";
import { Provider } from "mobx-react";

import "semantic-ui-css/semantic.min.css";
import "./assets/styles/global.scss";

import App from "./components/App/App";
import * as serviceWorker from "./serviceWorker";

import AppStore from "./stores/AppStore";
import Requests from "./core/Requests";

import Prod from "./env/prod";
import Dev from "./env/dev";

/**
 * Meaning we are starting from /tasks/$id/explore or /expert
 */
const Env = window.projectID ? Prod : Dev;

const app = AppStore.create(Env.getData(), {
  fetch: Requests.fetcher,
  patch: Requests.patch,
  post: Requests.poster,
  remove: Requests.remover,
  alert: m => console.log(m), // Noop for demo: window.alert(m)
});

app.initializeStore(Env.getState());

window.Htx = app;

ReactDOM.render(
  <Provider store={app}>
    <App />
  </Provider>,
  Env.rootElement(),
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
