import React from "react";
import { observer, inject } from "mobx-react";
import { Button } from "antd";

import Hint from "../Hint/Hint";
import styles from "./Controls.module.scss";

export default inject("store")(
  observer(({ store }) => {
    return (
      <div style={{ clear: "both" }}>
        <div className="ui hidden divider" />
        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
          <div className={styles.container}>
            {store.hasInterface("submit:skip") && (
              <Button type="ghost" onClick={store.skipTask} className={styles.skip}>
                Skip
                {store.settings.enableHotkeys && store.settings.enableTooltips && <Hint> [ Ctrl+Space ]</Hint>}
              </Button>
            )}
            <Button type="primary" icon="check" onClick={store.sendTask} className={styles.submit}>
              Submit
              {store.settings.enableHotkeys && store.settings.enableTooltips && <Hint> [ Ctrl+Enter ]</Hint>}
            </Button>
          </div>
          <div>{store.task && <h4 className={styles.task}>Task ID: {store.task.id}</h4>}</div>
        </div>
      </div>
    );
  }),
);
