import React from "react";
import { observer, inject } from "mobx-react";
import { types, getRoot } from "mobx-state-tree";

import { guidGenerator } from "../../utils/unique";
import Registry from "../../core/Registry";
import DialogView from "../../components/Dialog/Dialog";
import { runTemplate } from "../../core/Template";

/**
 * Dialog Tag. Render dialog
 * @example
 * <View>
 *  <Dialog name="dialog" value="$dialog"></Dialog>
 * <View>
 * @param {string} name
 * @param {object} value
 *
 * Example of data:
 * dialog: [
    {
      name: "Alice",
      text: "Hey, Bob!",
    },
    {
      name: "Bob",
      text: "How a u, Alice?",
    },
    {
      name: "Alice",
      text: "Things are great",
      selected: true,
    },
  ],
 */
const Replica = types.model({
  name: types.string,
  text: types.string,
  selected: types.optional(types.boolean, false),
});

const TagAttrs = types.model({
  value: types.maybeNull(types.string),
  name: types.maybeNull(types.string),
});

const Model = types
  .model({
    id: types.optional(types.identifier, guidGenerator),
    type: "Dialog",
    data: types.array(Replica),
  })
  .views(self => ({
    get completion() {
      return getRoot(self).completionStore.selected;
    },
  }));

const DialogModel = types.compose(
  "DialogModel",
  TagAttrs,
  Model,
);

const HtxDialogView = observer(({ store, item }) => {
  let result = [];

  // console.log(runTemplate(item.value, store.task.dataObj));

  // return <div dangerouslySetInnerHTML={{ __html: runTemplate(item.value, store.task.dataObj) }} />;

  if (!store.task.dataObj.dialog) {
    return "NOT DIALOG DATA";
  }

  store.task.dataObj.dialog.forEach((item, ind) => {
    result.push(<DialogView key={ind} name={item.name} text={item.text} selected={item.selected} />);
  });

  return <div style={{ display: "flex", flexFlow: "column" }}>{result}</div>;
});

const HtxDialog = inject("store")(observer(HtxDialogView));

Registry.addTag("dialog", DialogModel, HtxDialog);

export { DialogModel, HtxDialog };
